.AppContainer {
    height: 500px;
    width: 700px;
    padding: 5px;
    margin: 0px 200px;
    /*display: flex;*/
    justify-content: center;
}

.NavigationBox {
    padding: 10px;
    overflow: auto;
    border: 1px solid;
    border-radius: 5px;
    flex: 2;
    margin: 5px;
    height: 100%;
}

.MainBox {
    display: flex;
    flex: 8;
    flex-direction: column;
    height: 100%;
}

.ViewBox {
    height: 100%;
    flex: 10;
    user-select: none;
    overflow: auto;

    margin: 5px;
}

.EditingBox {
    padding: 10px;
    flex: 1;
    margin: 5px;
    border-radius: 5px;
    border: 1px solid;
}
